<template>
  <div class="site-wrapper">
    <div class="eat-info-page">
      <div class="container-fluid">
          <div class="row">
              <div class="section-1">
                  <div class="col-sm-10 col-12">
                      <div class="s1-video">
                          <video src="" poster="../assets/images/png/video1.png" class="my-video"></video>
                          <div class="s1-title">{{about.title}}</div>
                          <div class="play-button">
                              <img src="../assets/images/icons/play.svg" alt="">
                          </div>
                      </div>
                      <div class="my-text">{{about.description}}</div>
                      <div class="my-images">
                          <img src="../assets/images/png/eatinfo1.png" alt="">
                      </div>
                  </div>
              </div>
          </div>
      </div>
      <footer>
          <div class="my-footer">
              <div class="navbar-container">
                  <div class="row"></div>
              </div>
          </div>
      </footer>
  </div>
  </div>
</template>
<script>
    import axios from 'axios'
    import {API} from '../store/config'
    export default{
        data(){
            return{
                about:''
            }
        },
        mounted() {
            
            const token = localStorage.getItem('D-Token')
            axios .get(API+'dish/'+this.$route.params.id,
            
            {
                    headers: {
                    Authorization: `Bearer ${token}`
                  }

                }
                
            )
            .then(res=>{
                this.about=res.data.data
            })
        },
    }
</script>
<style>
  body{
    overflow-x: hidden !important;
  }
</style>
